import jss from "jss";
import preset from "jss-preset-default";

export default () => {
  jss.setup(preset());

  const style = {
    myButton: {
      color: "green",
    },
    "@global": {
      "#bcpo": {
        marginLeft: "15px",
      },
      "select.single-option-selector": {
        borderColor: "black",
      },

      "#shopify-section-header .promo-bar": {
        background: "black",
      },

      "#footer > div.f_mid": {
        background: "black",
        // color: "white!important",
        "& h4": {
          color: "white",
        },
        "& a": {
          color: "#cccccc",
          fontSize: "20px",
          "&:hover": {
            color: "white",
          },
        },
      },
      ".product_payments_btns>#addToCart": {
        color: "black",
        borderColor: "black",
      },
      ".product_payments_btns .shopify-payment-button__button--unbranded ": {
        backgroundColor: "black",
      },
      "#content .main_box .box_1 .on-sale": {
        backgroundColor: "black",
      },
    },
  };

  // Compile styles, apply plugins.
  const sheet = jss.createStyleSheet(style);

  // If you want to render on the client, insert it into DOM.
  sheet.attach();

  // If you want to render server-side, get the CSS text.
  // sheet.toString();
  // console.log(sheet.toString());

  const styleEl = document.createElement("style");
  document.head.appendChild(styleEl);
  styleEl.setAttribute("id", "aj-style");

  styleEl.innerHTML = `
  @media only screen and (min-width: 769px){
    nav.wsmenu ul.mobile-sub{
      display: none;
    }
  }
  @media only screen and (max-width: 768px){
    #desktopmenu{
      display: none;
    }
    nav.wsmenu{
      width: 80%;
      left: -80%;
    }
  }
  
.footer-policy div{
  text-align:center;
  display:inline-block; 
  padding:23px 0;
  height: 70px;
  overflow: hidden;
  
  display: flex;
  justify-content: center;idden
  align-items: center;

}

.footer-policy span{vertical-align: 2px;text-transform:uppercase;line-height:1.3em;letter-spacing:0.1em; }
.footer-policy div .fa{font-size:20px;margin-right:12px;}
.footer-policy div .fa-repeat{transform: rotate(220deg);-webkit-transform: rotate(220deg);-ms-transform: rotate(220deg);}
.footer-policy div:last-child{border:0}

#content .main_box .box_1 .product-image  img:last-child {
  display:none;
}

#content .main_box .box_1 .product-image a:hover img:first-child {
  display:none;
}
#content .main_box .box_1 .product-image a:hover img:last-child {
  display:inline-block ;
}
#content .main_box:hover .box_1 .product-image img, #content .main_box:hover .box_1 img{
  transform:none;
}

`;
};
